.results_inner_page{
    .section{
        &_1{
            border-bottom-right-radius: 100px;
            position: relative;
            z-index: 2;
            background: $white;
            padding-bottom: var(--spacing_1);
            &:after{
                transition: 2s;
                content: '';
                position: absolute;
                left: 0;
                top: -75px;
                background: $blue_1;
                height: 18%;
                width: 100%;
                border-bottom-left-radius: 400px;
                border-bottom-right-radius: 400px;
                z-index: -1;
                @include breakpoint(xxl) {
                    top: -55px;
                    height: 22%;
                }
                @include breakpoint(lg) {
                    top: -65px;
                    height: 300px;
                    border-bottom-left-radius: 200px;
                    border-bottom-right-radius: 200px;
                }
                @include breakpoint(md) {
                   height: 150px;
                }
                @include breakpoint(sm) {
                   top: -35px;
                   height: 50px;
                }
                @media(max-width: 400px){
                    height: 130px;
                }
            }
            &.enter{
                &:after{
                    height: 23%;
                    @include breakpoint(xxl) {
                        height: 28%;
                    }
                    @include breakpoint(lg) {
                        height: 500px;
                    }
                    @include breakpoint(md) {
                        height: 350px;
                    }
                    @include breakpoint(sm) {
                        height: 250px;
                    }
                    @media(max-width: 400px){
                        height: 230px;
                    }
                }
            }
            .inner_slider{
                position: relative;
                .article-slider_btns{
                    position: absolute;
                    top: 50%;
                    width: 110%;
                    left: -5%;
                    @media(max-width: 1355px){
                        position: relative;
                        top: 0;
                        left: 0;
                        width: fit-content;
                        margin: var(--spacing_10) auto;
                    }
                }
            }
            .swiper-wrapper{
                padding: var(--spacing_6) 0;
                img{
                    width: 100%;
                }
                .swiper-slide * {
                    cursor: initial !important;
                }
            }
            .container{
                > img{
                    margin: 0 auto;
                    width: 100%;
                }
                > .row{
                    display: flex;
                    justify-content: space-between;
                    padding: var(--spacing_6) 0;
                    color: $blue_3;
                }
            }
        }
        &_3{
            border-bottom-left-radius: 100px;
            margin-bottom: var(--spacing_6);
            &:after{
                display: none;
            }
        }
    }
}

.yellow_section{
    background: $yellow_1;
    position: relative;
    &::after{
        content: '';
        position: absolute;
        width: 100%;
        height: 60%;
        top: -57%;
        left: 0;
        background: $yellow_1;
    }
    .container{
        display: flex;
        align-items: center;
        padding-top: var(--spacing_6);
        padding-bottom: var(--spacing_6);
        @include breakpoint(md) {
            flex-direction: column;
            justify-content: center;
            text-align: center;
        }
        .btn-secondary{
            margin-left: auto;
            flex-shrink: 0;
            @include breakpoint(md) {
                margin: var(--spacing_6) auto 0;
            }
        }
        .title{
            max-width: 400px;
            margin-left: var(--spacing_6);
        }
    }
}

.insert_section{
    display: flex;
    padding: var(--spacing_6) 0;
    flex-wrap: wrap;
    @include breakpoint(md) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    > *{
        &:not(:last-child){
            padding-right: var(--spacing_2);
             @include breakpoint(md) {
                padding-right: 0;
                text-align: center;
            }
        }
    }
    .heading{
        color: $blue_1;
        font-size: 84px;
    }
    .subtitle_1{
        font-size: 28px;
        @include breakpoint(md) {
            text-align: center;
        }
    }
}